<template>
	<view>
		<cu-custom bgColor="theme-bg-color" :isBack="true">
			<block slot="backText">返回</block>
			<block slot="content">我的聊天列表</block>
		</cu-custom>
		<view class="cu-list menu-avatar" v-if="wechatItem.length>0">
			<template v-for="(item,index) in wechatItem">
				<view class="cu-item" @click="ToDetail(item.receiveUserId)">
					<view class="cu-avatar round lg" :style="[{ backgroundImage:'url(' + item.receiveAppUserDto.imageUrls + ')' }]">
					</view>
					<view class="content">
						<view class="text-grey">{{item.receiveAppUserDto.name}}</view>
						<view class="text-gray text-sm flex">
							<view class="text-cut">
								<text class="cuIcon-infofill text-red  margin-right-xs"></text>
								{{item.lastMessage}}
							</view>
						</view>
					</view>
					<view class="action">
						 <view class="text-grey text-xs">{{item.waitRead==0?"已读":"未读"}}</view> 
						<view class="cu-tag round bg-grey sm">{{item.lastMessageDateTimeString}}</view>
					</view>
				</view>
			</template>
		</view>



		<view v-else>
			<cu-no-data></cu-no-data>

		</view>
		<view style="height:120upx"></view>
		<!-- 	<view class="publish" @click="Publish">
			<text class="lg text-gray cuIcon-edit" style="color:white;font-size: 60upx;"></text>
		</view> -->

	</view>
</template>

<script>
	import store from "@/store";
	import {
		mapGetters
	} from "vuex";
	export default {
		data() {
			return {
				isCard: false,
				TabCur: 0,
				wechatItem: [],
				keyword: ""
			}
		},
		computed: {
			...mapGetters(["userInfo"]),
		},
		async onShow() {


			await this.WechatList();

		},
		methods: {


			async WechatList() {
				let url = "/Wechat/GetWechatListByUserId";

				this.$comm.ShowLoading();
				let {
					success,
					data: {
						items
					}
				} = await this.$http.PostAsync(url, {userId:this.userInfo.id});
				if (success == true) {
					this.wechatItem = items;
				}
				uni.hideLoading()
			},


			async ToDetail(id) {
				if (this.$comm.CheckToken()) {
					if (this.userInfo.id == id) {
						this.$comm.ShowToast("不能够咨询自己哦");

					} else {
						uni.navigateTo({
							url: `/pages/wechat/wechat?id=${id}`
						})
					}
				}
			},

		}
	}
</script>

<style scoped>
	.publish {
		display: flex;
		align-items: center;
		justify-content: center;
		background-color: #1CBBB4;
		width: 100upx;
		height: 100upx;
		border-radius: 50%;
		position: fixed;
		right: 30upx;
		bottom: 150upx;
	}
</style>
